function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.main-slider .swiper-button-next',
            prevEl: '.main-slider .swiper-button-prev',
        },
        pagination: {
            el: '.main-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function projectSlider() {
    var swiper = new Swiper('.project-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        navigation: {
            nextEl: '.project-slider .swiper-button-next',
            prevEl: '.project-slider .swiper-button-prev',
        },
    })
}

function productsSlider() {
    var swiper = new Swiper('.products .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.products .swiper-button-next',
            prevEl: '.products .swiper-button-prev',
        },
        pagination: {
            el: '.products .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function collectionSlider() {
    var swiper = new Swiper('.collection .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.collection .swiper-button-next',
            prevEl: '.collection .swiper-button-prev',
        },
        pagination: {
            el: '.collection .swiper-pagination',
            type: 'progressbar',
        },
        scrollbar: {
            el: '.collection .swiper-scrollbar',
            draggable: true,
        },
    })
}

function objectsSlider() {
    var swiper = new Swiper('.objects .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        direction: "vertical",
        loop: true,
        navigation: {
            nextEl: '.objects .swiper-button-next',
            prevEl: '.objects .swiper-button-prev',
        },
        pagination: {
            el: '.objects .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                direction: "horizontal",
            },
            992: {
                direction: "vertical",
            }
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical",
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 0,
        slidesPerView: "auto",
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
    });
}



mainSlider()
productsSlider()
collectionSlider()
objectsSlider()
gallery()
projectSlider()
// Sliders()
